html {
  font-size: 16px;
}

body {
  margin: 0;
}

/* Reset CSS */
h1,
h2,
h3,
h4,
h5,
h6,
input {
  padding: 0;
  margin: 0;
}

div,
header,
section,
footer {
  box-sizing: border-box;
}

.App {
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: rgb(246, 246, 246);
}

.practice {
  background-color: rgb(204, 204, 204);
}
